import { Selection, TextSelection } from "prosemirror-state";
import { Slice } from "prosemirror-model";
import { ReplaceStep, Mapping } from "prosemirror-transform";

var markDeletion = function markDeletion(tr, from, to, user, username, date1, date10) {
  var deletionMark = tr.doc.type.schema.marks.deletion.create({
    user: user,
    username: username,
    date: date10
  });
  var firstTableCellChild = false;
  var deletionMap = new Mapping(); // Add deletion mark to block nodes (figures, text blocks) and find already deleted inline nodes (and leave them alone)

  tr.doc.nodesBetween(from, to, function (node, pos) {
    if (pos < from && node.type.name === "table_cell") {
      firstTableCellChild = true;
      return true;
    } else if (pos < from && node.isBlock || firstTableCellChild) {
      firstTableCellChild = false;
      return true;
    } else if (["table_row", "table_cell"].includes(node.type.name)) {
      return false;
    } else if (node.isInline && node.marks.find(function (mark) {
      return mark.type.name === "insertion" && mark.attrs.user === user && !mark.attrs.approved;
    })) {
      var removeStep = new ReplaceStep(deletionMap.map(Math.max(from, pos)), deletionMap.map(Math.min(to, pos + node.nodeSize)), Slice.empty);

      if (!tr.maybeStep(removeStep).failed) {
        deletionMap.appendMap(removeStep.getMap());
      }
    } else if (node.isInline && !node.marks.find(function (mark) {
      return mark.type.name === "deletion";
    })) {
      tr.addMark(deletionMap.map(Math.max(from, pos)), deletionMap.map(Math.min(to, pos + node.nodeSize)), deletionMark);
    } else if (node.attrs.track && !node.attrs.track.find(function (trackAttr) {
      return trackAttr.type === "deletion";
    }) && !["bullet_list", "ordered_list"].includes(node.type.name)) {
      if (node.attrs.track.find(function (trackAttr) {
        return trackAttr.type === "insertion" && trackAttr.user === user;
      })) {
        var _removeStep; // user has created element. so (s)he is allowed to delete it again.


        if (node.isTextblock && to < pos + node.nodeSize) {
          // The node is a textblock. So we need to merge into the last possible position inside the last text block.
          var selectionBefore = Selection.findFrom(tr.doc.resolve(pos), -1);

          if (selectionBefore instanceof TextSelection) {
            _removeStep = new ReplaceStep(deletionMap.map(selectionBefore.$anchor.pos), deletionMap.map(to), Slice.empty);
          }
        } else {
          _removeStep = new ReplaceStep(deletionMap.map(Math.max(from, pos)), deletionMap.map(Math.min(to, pos + node.nodeSize)), Slice.empty);
        }

        if (!tr.maybeStep(_removeStep).failed) {
          deletionMap.appendMap(_removeStep.getMap());
        }
      } else {
        var track = node.attrs.track.slice();
        track.push({
          type: "deletion",
          user: user,
          username: username,
          date: date1
        });
        tr.setNodeMarkup(deletionMap.map(pos), null, Object.assign({}, node.attrs, {
          track: track
        }), node.marks);
      }
    }
  });
  return deletionMap;
};

export default markDeletion;